/* Customize your main colors in :root variables */
:root {
  --main-background-color: #ffffff;
  --card-background-color: #000000;
  --countdown-background-color: #433765;
  --main-text-color:#F7F6F4;
  --title-text-color:#fb2d2d;
}

body{
/*  background-color:var(--main-background-color);*/
  background: url("img/background.png");
  background-size: cover;
  font-family: 'JetBrains Mono', cursive;
  font-size: 1;
}

@font-face {
  font-family: 'DOWN';
  src: url('./ashcan-bb.regular.ttf') format('truetype');
}

@font-face {
  font-family: 'scratchfont';
  src: url('./scratch.ttf') format('truetype');
}

h2, p{
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}



